"use server";

import * as pdfjs from "pdfjs-dist";
import "pdfjs-dist/build/pdf.worker.mjs";
import { db } from '@/lib/db-config';
import { documents } from '@/lib/db-schema';
import { genrateEmbeddings, genrateEmbeddingsMany } from '@/lib/embeddings';
import { chunkcontent } from '@/lib/chunking';
import { tr } from "zod/v4/locales";

export async function processpdfFile(formData: FormData) {
    try {
        const file = formData.get("pdf") as File; 

        const bytes = await file.arrayBuffer();
        const buffer = Buffer.from(bytes);
        const data = await pdfjs(buffer);

        if (!data.text || data.text.trim().length === 0) {
            return {
                success: false,
                error: "The uploaded PDF file contains no extractable text."
            }
        }

        const chunks = await chunkcontent(data.text);
        const embeddings = await genrateEmbeddingsMany(chunks);

        const record = chunks.map((chunk, index) => ({
            content: chunk,
            embedding: embeddings[index],
        }));
        await db.insert(documents).values(record);

        return ({
            success: true,
            message: "PDF file processed and data stored successfully." 
        })
    }
    catch (error) {
        console.error("Error processing PDF file:", error);
        return {
            success: false,
            error: "Failed to process PDF file."
        }
    }
}